import React, { useEffect, useState } from "react";
import { Actor, HttpAgent } from "@dfinity/agent";
import { idlFactory as backend_idl } from "./declarations/backend_backend/backend_backend.did.js";
import { canisterId as backendCanisterId } from "./declarations/backend_backend";

const agent = new HttpAgent({ host: "http://localhost:4943" });
agent.fetchRootKey(); // Only for local development

const backendActor = Actor.createActor(backend_idl, {
  agent,
  canisterId: backendCanisterId,
});

function App() {
  const [message, setMessage] = useState("");

  useEffect(() => {
    async function callBackend() {
      try {
        const response = await backendActor.issueCertificate("Sachin", "Blockchain 101");
        setMessage("Certificate issued successfully");
      } catch (err) {
        console.error("Error calling backend:", err);
        setMessage("Error: " + err.message);
      }
    }

    callBackend();
  }, []);

  return (
    <div style={{ padding: "2rem", fontFamily: "sans-serif" }}>
      <h1>ICP React Frontend</h1>
      <p>{message ? message : "Loading..."}</p>
    </div>
  );
}

export default App;
